.header {
    height: 80px;
    background-color: lightblue;
    display: flex;
    justify-content: center;
}
.container {
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
@media (max-width: 441px) {
    .container {
        width: 100%
    }
}